@import url(https://fonts.googleapis.com/css?family=Indie+Flower|Josefin+Sans|News+Cycle|Pacifico&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, sans-serif;
}

input {
  max-width: 500px;
}

.gray {
  color: #828282;
}

.orange {
  background-color: #7188d4;
}

.background-gray {
  background-color: rgb(246, 246, 239);
}

.options {
  margin-left: 5px;
  margin-right: 2px;
  color: black;
}

.options:hover {
  color: white;
}

.title {
  font-weight: bold;
}

.f11 {
  font-size: 15px;
  padding: 5px;
}

.f11:hover {
  color: rgb(173, 233, 173);
  cursor: pointer;
}

.w85 {
  width: 85%;
}

.button {
  font-family: monospace;
  font-size: 10pt;
  color: black;
  background-color: buttonface;
  text-align: center;
  padding: 2px 6px 3px;
  border-width: 2px;
  border-style: outset;
  border-color: buttonface;
  cursor: pointer;
  max-width: 250px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
h1 {
  padding-top: 20px;
  margin: 0;
}

p {
  margin: 0;
  padding: 0;
  font-family: "News Cycle";
  color: white;
}

p:hover {
  color: rgb(38, 133, 125);
}

h4 {
  color: white;
  padding-bottom: 20px;
  padding-left: 25px;
  padding-right: 25px;
  margin: 0;
  font-family: "News Cycle";
}
.main {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.header {
  width: 100vw;
  background-color: rgb(46, 29, 53);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  color: white;
  font-family: "News Cycle";
  font-size: 200%;
}

.log {
  margin-right: 20px;
}

.linkOptions {
  display: flex;
}

.submitLink {
  display: flex;
}

.allOptions {
  padding-left: 20px;
  padding-bottom: 20px;
  padding-top: 20px;
  display: flex;

  justify-content: space-between;
}
.linkItems {
  width: 100vw;
  padding: 10px;
}
.linksToShow {
  font-family: "News Cycle";
}
.displayThese {
  display: flex;
}

a:hover {
  color: rgb(38, 133, 125);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media only screen and (max-width: 600px) {
  .title {
    font-size: 130%;
  }
}

